import {useState, useEffect, useRef} from 'react';
import moment from 'moment';

import styles from './styles.module.scss';

let now = moment(new Date()); //todays date
let end = moment('28/11/2021', 'DD/MM/YYYY'); // another date
let duration = moment.duration(end.diff(now));
let seconds = duration.asSeconds();
console.log('days: ', seconds);

const FormGreetings = () => {
    const totalSeconds = useRef(seconds);
    const timer = useRef(0);
    const [time, setTime] = useState({});

    const secondsToTime = (secs) => {
        let days = Math.floor(secs / (24 * 60 * 60));
        let hours = Math.floor((secs / (60 * 60)) % 24);

        let divisor_for_minutes = secs % (60 * 60);
        let minutes = Math.floor(divisor_for_minutes / 60);

        let divisor_for_seconds = divisor_for_minutes % 60;
        let seconds = Math.ceil(divisor_for_seconds);

        let obj = {
            d: days,
            h: hours,
            m: minutes,
            s: seconds,
        };
        console.log('obj: ', obj);
        return obj;
    };

    useEffect(() => {
        let timeLeftVar = secondsToTime(totalSeconds.current);
        setTime({...timeLeftVar});
        startTimer();
    }, []);

    const startTimer = () => {
        if (timer.current === 0 && totalSeconds.current > 0) {
            timer.current = setInterval(countDown, 1000);
        }
    };

    const countDown = () => {
        // Remove one second, set state so a re-render happens.
        totalSeconds.current = totalSeconds.current - 1;
        setTime({
            ...secondsToTime(totalSeconds.current)
        });

        // Check if we're at zero.
        if (totalSeconds.current === 0) {
            clearInterval(timer.current);
        }
    };

    return (
        <div className='et_pb_section et_pb_section_4 et_pb_with_background et_section_regular'>
            <div className='et_pb_with_border et_pb_row et_pb_row_6 et_animated et_pb_equal_columns et_pb_gutters3'>
                <div className='et_pb_column et_pb_column_4_4 et_pb_column_10 et_pb_css_mix_blend_mode_passthrough et-last-child'>
                    <div className='et_pb_module et_pb_countdown_timer et_pb_countdown_timer_0 et_pb_no_bg et_pb_bg_layout_light'>
                        <div className={`et_pb_countdown_timer_container clearfix ${styles['countdown-timer']}`}>
                            <div className='days section values' data-short='Day' data-full='Day(s)'>
                                <p className={styles['value']} >{time.d}</p>
                                <p className='label'>Day(s)</p>
                            </div>
                            <div className='sep section'>
                                <p>:</p>
                            </div>
                            <div className='hours section values' data-short='Hrs' data-full='Hour(s)'>
                                <p className={styles['value']} >{time.h}</p>
                                <p className='label'>Hour(s)</p>
                            </div>
                            <div className='sep section'>
                                <p>:</p>
                            </div>
                            <div className='minutes section values' data-short='Min' data-full='Minute(s)'>
                                <p className={styles['value']}>{time.m}</p>
                                <p className='label'>Minute(s)</p>
                            </div>
                            <div className='sep section'>
                                <p>:</p>
                            </div>
                            <div className='seconds section values' data-short='Sec' data-full='Second(s)'>
                                <p className={styles['value']}>{time.s}</p>
                                <p className='label'>Second(s)</p>
                            </div>
                        </div>
                    </div>
                    <div className='et_pb_module et_pb_text et_pb_text_5 et_pb_text_align_center et_pb_bg_layout_light'>
                        <div className='et_pb_text_inner'>
                            <h2>We Can’t Wait To Celebrate With You!</h2>
                            <p>We’re Tying The Knot on June 24th in San Francisco, CA</p>
                        </div>
                    </div>
                    <div id='et_pb_contact_form_0' className='et_pb_module et_pb_contact_form_0 et_pb_contact_form_container clearfix' data-form_unique_num={0}>
                        <div className='et-pb-contact-message' />
                        <div className='et_pb_contact'>
                            <form className='et_pb_contact_form clearfix' method='post' action='https://www.elegantthemes.com/layouts/events/wedding-engagement-home-page/live-demo'>
                                <p className='et_pb_contact_field et_pb_contact_field_0 et_pb_contact_field_last' data-id='name' data-type='input'>
                                    <label htmlFor='et_pb_contact_name_0' className='et_pb_contact_form_label'>
                                        Name
                                    </label>
                                    <input type='text' id='et_pb_contact_name_0' className='input' defaultValue name='et_pb_contact_name_0' data-required_mark='required' data-field_type='input' data-original_id='name' placeholder='Name' />
                                </p>
                                <p className='et_pb_contact_field et_pb_contact_field_1 et_pb_contact_field_last' data-id='email' data-type='email'>
                                    <label htmlFor='et_pb_contact_email_0' className='et_pb_contact_form_label'>
                                        Email Address
                                    </label>
                                    <input type='text' id='et_pb_contact_email_0' className='input' defaultValue name='et_pb_contact_email_0' data-required_mark='required' data-field_type='email' data-original_id='email' placeholder='Email Address' />
                                </p>
                                <p className='et_pb_contact_field et_pb_contact_field_2 et_pb_contact_field_last' data-id='rsvp' data-type='select'>
                                    <label htmlFor='et_pb_contact_rsvp_0' className='et_pb_contact_form_label'>
                                        Will You Be Attending?
                                    </label>
                                    <select id='et_pb_contact_rsvp_0' className='et_pb_contact_select input' name='et_pb_contact_rsvp_0' data-required_mark='required' data-field_type='select' data-original_id='rsvp'>
                                        <option value>Will You Be Attending?</option>
                                        <option value='Yes'>Yes</option>
                                        <option value='No'>No</option>
                                    </select>
                                </p>
                                <input type='hidden' defaultValue='et_contact_proccess' name='et_pb_contactform_submit_0' />
                                <div className='et_contact_bottom_container'>
                                    <button type='submit' name='et_builder_submit_button' className='et_pb_contact_submit et_pb_button'>
                                        submit
                                    </button>
                                </div>
                                <input type='hidden' id='_wpnonce-et-pb-contact-form-submitted-0' name='_wpnonce-et-pb-contact-form-submitted-0' defaultValue='8128da3d84' />
                                <input type='hidden' name='_wp_http_referer' defaultValue='/layouts/events/wedding-engagement-home-page/live-demo' />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default FormGreetings;
